export default class Inpup {
	constructor(email) {
		this.email = document.querySelectorAll(email);
	}

	checkInput() {
		this.email.forEach(item => {
			item.addEventListener('keypress', (e) => {
				if (!e.key.match(/[^-]/ig)) {
					e.preventDefault();
				}
			})

			item.addEventListener('input', () => {
				if (!item.value.match(/[^-]/ig)) {
					item.value = ''
				}
			})
		})
	}
}